import misEnum from '../../common/mis-enum.js';
export default {
	data() {
		return {
			type: 0,
			cars: [],
			TabCur: 0,
			StatusEnumMap: misEnum.CarTypeEnumMap
		}
	},
	onLoad(option) {
		if (option.type) {
			var index = 0;
			misEnum.CarTypeEnumMap.forEach((value, key, map) => {
				if (key == option.type) {
					this.TabCur = index;
					return;
				}
				index++;
			});
			this.type = option.type;
		} else {
			this.type = Array.from(this.StatusEnumMap.keys())[0];
		}
	},
	onShow() {
		this.typeTab(this.TabCur);
	},
	methods: {
		loadData(){
			global.$http.post('/car/info/carList', {
				params: {
					page: 1,
					pageSize: 1000,
					car_type: this.type
				},
			}).then(res => {
				if (res.status === "0") {
					this.cars = res.data.list;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		typeTab: function(index) {
			this.TabCur = index;
			this.type = Array.from(this.StatusEnumMap.keys())[index];
			this.cars = [];
			this.loadData();
		},
		toAdd: function(e){
			uni.navigateTo({
				url:'../work/saveCar'
			});
		},
		carDetail: function(car){
			uni.navigateTo({
				url: "../work/carDetail?id=" + car.id
			});
		}
	}
}
